.App {
  text-align: center;
  overflow: hidden;

}
.HeaderContainer {
  
  margin: 0;
    align-items: center;
    width: 100%;
}
.back{
  background-color: #FDFDFD;
}
.HeadTitle {
  font-family: Farmhouse;
  font-size: 2rem;
  color: #2c2929;
  text-align: center;
  justify-content: center;
}
.signers {
  width: 50rem;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
.customTitle {
  display: flex;
  height: 10rem;
  margin-bottom: 0;
}
@font-face {
  font-family: Farmhouse;
  src: url(./Farmhouse.otf);
}
.welcomeMessage {
  margin-top: 15rem;
  text-align: left;
  flex-direction: column;
  align-items: center;
  display: flex;
  justify-content: space-between;
  color: rgb(31, 31, 31);
  padding: 5rem;
}
.welcomeMessage h1 {
  font-family: Helvetica;
  font-weight: bolder;
  font-size: 2rem;
  color: #32353f;
  width: 40rem;
  text-align: justify;
}
.welcomeMessage h2 {
  font-family: Helvetica;
  font-size: 1.5rem;
  font-weight: lighter;
  color: #5e5c5c;
  width: 40rem;
  text-align: left;
}
body{
  display: flex;
}
.welcomeMessage span {
  font-family: Helvetica;
  font-size: 2rem;
  color: #5879e3;
  width: 50rem;
}
.contain {
  background-color: #282c34;
  width: 100%;
}
.mainSplash {
  width: 50rem;
  height: auto;
  flex-direction: column;
}
.main {
  display: flex;
  background-color: #FDFDFD;
  height: 100vh;
  width: 100%;
  margin-top: -3rem;
  justify-content: center;
  align-items: center;
}
.mainLogo {
  display: flex;
  width: 30rem; /* Adjust this value according to your preference */
  margin-top: -5rem;
}
.mainContainer {
  align-items: center;
  text-align: center;
  flex-direction: column;
  display: flex;
  justify-content: center;
  color: rgb(31, 31, 31);
  width: 100%;
  padding: 5rem;
  margin-top: 3rem;
  background-color: #FDFDFD;
}

/* Mobile devices */
@media only screen and (max-width: 800px) {
  .mainContainer, .main, .welcomeMessage h1, .welcomeMessage h2, .welcomeMessage span {
    margin-left: auto;
    margin-right: auto;
    margin-top: -1rem;
  }
  .leftIcon{
    margin-left: 10rem
  }
  .HeaderContainer{
font-size: larger
;  }
  .mainContainer {
    align-items: center;
  }
  .HeadTitle {
    font-size: 1rem;
    width: 200%;
  }
  .signers {
    width: 40rem;
    height: 5rem;
    font-size:20rem ;
    margin-left: auto;
    margin-right: auto;
  }
  .foot{
    margin-bottom: -20rem;
  }
  .mainLogo {
    width: 30rem; /* You may adjust this value */
    margin-bottom:-30rem;
  }
}
.foot{
}
/* Large screens */
@media only screen and (min-width: 801px) {
  .mainContainer {
    flex-direction: row;
  }
  .signers {
    width: 40rem;
    margin-left: auto;
    margin-right: auto;
  }
  .welcomeMessage {
    margin-top: -5rem;
  }
}
.imageContainer {
  width: 50rem;
  height: auto;
  flex-direction: row;
}
.Logo {
  margin: 1rem;
  width: 5rem;
  height: 5rem;
  z-index: 10;
}
